.box-shadow{
    box-shadow: 0 18px 88px -4px rgba(24,39,75,.14), 0 8px 28px -6px rgba(24,39,75,.12);
}

.income-container{

    .data-container{
        min-width: 20%;
        background-color: white;
        padding: 15px 10px 15px 10px;
        border-radius: 6px;
    }

    .icon-border{
        border-radius: 50%;
        width: 75px;
        height: 75px;

        div{
            padding: 21px;
            font-size: 33px;
        }
    }

    .data{
        .heading{
            color: #D5D5D5;
            font-size: 11px;
            padding-bottom: 3px;
        }
        .time{
            color: #6A6C71;
        }
        .price{
            padding-bottom: 2px;
        }
    }
}

.overview-container{

    .upper{
        select{
            color: #8f8c8c;
            background-color: #f2efff87;
            padding: 4px;
            border-radius: 8px;
        }
    }

    p{
        color: #8f8c8c85;
        font-weight: bold;
        margin-top: 6px;
    }
}

.customer-container{
    min-height: 426px;

    .head{
        color: #8f8c8c85;
        font-weight: bold;
        margin-top: 6px;
    }

    .dougnut-text{
        top: 40%;
        left: 40%;
    }

}